@import "~antd/dist/antd.css";
@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap");

:root {
  --primary-color: #005df2;
  --primary-color-hover: #0062ff;
  --primary-color-active: #163e72;
  --secondary-color: #6952dc;
  --secondary-color-hover: #755fe2;
  --secondary-color-active: #393d69;
  --main-color-1: #25c685;
  --main-color-hover-1: #3dd598;
  --main-color-active-1: #286053;
  --main-color-2: #ffbc25;
  --main-color-hover-2: #ffc542;
  --main-color-active-2: #625b39;
  --main-color-3: #ff8a34;
  --main-color-hover-3: #ff974a;
  --main-color-active-3: #624d3b;
  --main-color-4: #ff464f;
  --main-color-hover-4: #ff575f;
  --main-color-active-4: #623a42;
  --bg-color: #1f2e35;
  --primary-text-color: #96a7af;
  --primary-text-color-heading: #fff;
  --card-bg: #2a3c44;
  --card-bg-active: linear-gradient(
    153deg,
    rgba(34, 52, 60, 1) 0%,
    rgba(31, 46, 53, 1) 100%
  );
}

#root {
  display: flex;
  justify-content: center;
  background: var(--bg-color);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Lato", "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

span,
h1,
p,
input,
label,
.ant-form-item .ant-form-item-label > label {
  color: var(--primary-text-color);
}

h1 {
  text-align-last: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--primary-text-color-heading);
}

p {
  text-align: left;
}

.df {
  display: flex;
}

.jcc {
  justify-content: center;
}

.fdc {
  flex-direction: column;
}

.fdc .ant-form-item-control-input-content {
  display: flex;
  flex-direction: column;
}

.aifs {
  align-items: flex-start;
}

.main-view {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;
  padding: 0;
}

.main-view {
  width: 400px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

li {
  color: var(--primary-text-color-heading);
  text-align: left;
}

.main-view li {
  list-style: none;
  padding-bottom: 30px;
}

.main-view a {
  color: var(--primary-text-color);
  transition: all 0.3s ease;
  font-size: 16px;
}

.main-view a:hover {
  color: var(--main-color-hover-1);
}

.ant-form-item .ant-select.new-game__selector {
  width: 100%;
}

.ant-form-item-control-input-content {
  display: flex;
  justify-content: flex-end;
}

.new-game__submit {
  width: 100%;
}

.role-managing__card-wrapper {
  width: 300px;
  min-height: 480px;
  position: relative;
}

.role-managing__card.ant-card-bordered {
  width: 300px;
  min-height: 480px;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: var(--primary-color);
  border-color: var(--primary-color);
  cursor: pointer;
  transition: all 0.3s ease;
  background: var(--card-bg-active);
  border: none;
  border-radius: 5px;
}

.ant-card-body {
  color: var(--primary-text-color);
}

.role-managing__card--opened.ant-card-bordered.card {
  background: var(--card-bg);
  box-shadow: 0 1px 14px 0 #19282f;
}

.role-managing__meta .ant-card-meta-title {
  color: white;
}

.role-managing__meta .ant-card-meta-description {
  color: rgba(255, 255, 255, 0.8);
}

.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  align-items: center;
  padding: 40px 21px;
  color: --var(--primary-text-color);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.df {
  display: flex;
}

.aic {
  align-items: center;
}

.jcsb {
  justify-content: space-between;
}

.ant-input-number,
.ant-input {
  width: 100%;
  background: var(--card-bg);
  border: none;
  color: white;
}

.ant-btn > span {
  color: var(--primary-text-color-heading);
}

.ant-select-selector {
  background: var(--card-bg);
  border: none;
}

.ant-select-multiple .ant-select-selection-item {
  background: var(--card-bg);
  border: none;
}

.ant-select:not(.ant-select-disabled):hover .ant-select-selector,
.ant-select:not(.ant-select-customize-input) .ant-select-selector {
  border: none;
  background: var(--card-bg);
}

.ant-select-multiple .ant-select-selection-item-remove svg {
  fill: var(--primary-text-color);
}

.ant-btn {
  background: var(--main-color-1);
  border: none;
  padding: 21px;
  height: auto;
  line-height: 1em;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 12px;
  font-size: 16px;
  font-weight: bold;
}

.ant-btn.ant-btn-link {
  background: transparent;
}

.ant-btn[disabled],
.ant-btn[disabled]:hover,
.ant-btn[disabled]:focus,
.ant-btn[disabled]:active {
  background: var(--main-color-hover-1);
  opacity: 0;
}

.ant-btn.ant-btn-link:hover,
.ant-btn.ant-btn-link:focus,
.ant-btn.ant-btn-link:active {
  background: var(--main-color-active-1);
}

.ant-btn[disabled],
.ant-btn[disabled]:hover,
.ant-btn[disabled]:focus,
.ant-btn[disabled]:active {
  background: var(--main-color-hover-1);
  opacity: 0;
}

.ant-btn:hover,
.ant-btn:focus,
.ant-btn:active {
  background: var(--main-color-hover-1);
}

.game__counter {
  font-size: 80px;
}

.ant-btn > a:only-child {
  color: var(--primary-text-color-heading);
}

.ant-btn.btn-shadowed {
  background-color: var(--main-color-active-1);
}

.ant-btn.btn-shadowed > span,
.ant-btn.btn-shadowed a {
  color: var(--main-color-1);
}

.ant-btn.btn-error {
  background-color: var(--main-color-4);
}

.ant-btn.btn-error > span,
.ant-btn.btn-error a {
  color: var(--primary-text-color-heading);
}

.ant-btn.btn-small {
  padding: 16px;
  font-size: 14px;
}

.ant-btn.btn-xsmall {
  padding: 10px;
  font-size: 12px;
}

.game__result,
.game__controls {
  display: grid;
  grid-gap: 5px;
  grid-template-columns: 1fr 1fr;
  padding: 10px 0;
}

.winner__people .role-managing__card.ant-card-bordered h1,
.winner__people .role-managing__card.ant-card-bordered span {
  color: var(--main-color-1);
}

.winner__spy .role-managing__card.ant-card-bordered h1,
.winner__spy .role-managing__card.ant-card-bordered span {
  color: var(--main-color-4);
}